import React, { useEffect, useState } from "react";
import Table from "../../../share_components/Table";
import { TableActionBoutton, filterSearch, getList } from "../../../api_services/SharedServices";
import RoleModal from "./RoleModal";
import { UseFormValidations } from "../../../validations/UseFormValidation";
import { MdToggleOff, MdToggleOn } from "react-icons/md";
import { BiEdit } from "react-icons/bi";
import { urls } from "../../../api_services/url";
import { ListOfCards } from "../../../share_components/ListOfCards";

const RoleMaster = () => {
  const [search, setSearch] = useState("");
  const [modal, setModal] = useState(false);
  const [update, setUpdate] = useState("");
  const [getRolesData, setGetData] = useState([]);

  const getRolesList = async () => {
    let jsonObjects = {
      roleId: 0,
    };
    let res = await getList(urls?.rolemaster?.getList, { jsonObjects });

    setGetData(res);
  };

  const { data,headerlink, handleChangeSearch } = UseFormValidations({});
  useEffect(() => {
    headerlink([
      {
        name: "Masters",
        link: "/outpatientpro/admin/rolemaster",
        active: true,
      },
    ]);
  }, []);
  const columns = [
    {
      name: "Name",
      selector: (row) => (
        <div className="pointer" title={row?.roleName}>
          {row?.roleName}
        </div>
      ),
      sortable: true,
      key: "roleName",
    },

    {
      name: "Action",
      


      selector: (row) => (
        <>
          {TableActionBoutton([
            {
              name: "Edit",
              modalName: setModal,
              value: row,
              icon: <BiEdit className="" color="#0073EE" size={20} />,
            },
          ])}
        </>
      ),

      width: "15rem",
    },
  ];

  useEffect(() => {
    getRolesList();
  }, [update, data?.type]);

  return (
    <>
      <div className=" row p-3 bg-white ">
        <div className="col-xl-8 col-lg-6  col-md-3 mobile_Header">
          <div className=" f30 medium">Role Masters</div>
        </div>
        <div className="col-md-3 show_header">
          <div className="m-0 p-0 f20mobile  medium ">Role Masters</div>
        </div>
        <div className="col-xl-3  col-lg-4  col-md-7 py-1">
          {filterSearch(setSearch, search)}
        </div>

        <div className="col-xl-1 col-lg-2  col-md-2 py-1">
          <div
            className="border save rounded pointer text-white text-center p-2 px-3"
            onClick={() => setModal(true)}
          >
            Add Role
          </div>
        </div>
    

  <div className="mobile_Header">    <Table
        dataTable={getRolesData || []}
        columns={columns}
        search={search}
        pege={"10"}
      /></div>

      {modal && (
        <RoleModal
          show={modal}
          update={setUpdate}
          masterName={data?.type}
          onHide={() => setModal(false)}
        />
      )}
        </div>


        <div className="show_header">
        {" "}
        <ListOfCards
          array={getRolesData || []}
          setModal={setModal}
          title={"Masters"}
          name={"RoleMaster"}
        />
      </div>
    </>
  );
};

export default RoleMaster;
